import React, { useState, useEffect } from 'react';
import styles from '../../styles/Desktop.module.css';
import Recepti from '../ApplicationContent/Recepti';
import Messenger from '../ApplicationContent/Messenger';
import WeightGraph from '../ApplicationContent/WeightGraph';

import * as firebase from 'firebase/app';

//let data: any;
const Users = (props) => {
  
  let userId = props.data // getting UID
  //let ind = props.idata // getting index WHY ??
  
  const [showRecepti, setShowRecepti] = useState(false);
  const [showMessages, setShowMessages] = useState(false);
  const [showWeightGraph, setShowWeightGraph] = useState(false);
  //const [userUID, setUserUID] = useState();

  useEffect(() => {
    console.log("how many times it turns off FB.DB.off")
    firebase.database().ref('/users/').off("value");
  }, [])

  // removes recepti when u click on new user !!
  useEffect(() => {
    setShowRecepti(false);
    setShowMessages(false);
    setShowWeightGraph(false);
  }, [userId])

  // when clicking on recepti 
  //let recepti = null;
  //let messages = null;

  const getReceptiHandler = () => {
    //setUserUID(userId)
    setShowMessages(false);
    setShowWeightGraph(false);
    setShowRecepti(true);
    console.log("Recepti click")
    //console.log("RCUID: " + userUID);
    console.log("RCId: " + userId)
  }
  const getMessagesHandler = () => {
    //setUserUID(userId)
    setShowRecepti(false);
    setShowWeightGraph(false);
    setShowMessages(true);
    console.log("Messages click")
    //console.log("RCUID: " + userUID);
    console.log("RCId: " + userId)
  }
  const getWeightHandler = () => {
    //setUserUID(userId)
    setShowRecepti(false);
    setShowMessages(false);
    setShowWeightGraph(true);
    console.log("Weight graph click")
    //console.log("RCUID: " + userUID);
    console.log("RCId: " + userId)
  }
  // if (showRecepti) {
  //   recepti = (
  //     <Recepti data={userId} />
  //   )
  // }
  // if (showMessages) {
  //   messages = (
  //     <Messenger data={userId} />
  //   )
  // }

  return  (
    <div className={styles.Desktop}>
      <div className={styles.List}>
        {
          // for now all LIST then SEPERATE and put UID INSIDE !!
          // <p>{userId}</p>
        }
        <div className={styles.DesktopIcons}>
          <img className={styles.Icons} src={require('../../icons/weight.png')} alt="Meritve icon" />
          <h4>Meritve</h4>
        </div>
        <div className={styles.DesktopIconsSmall}> 
        <img className={styles.IconsS} src={require('../../icons/weight.png')} alt="Weight icon" />
          <p onClick={() => getWeightHandler()}>Telesna Masa</p>
        </div>
        <div className={styles.DesktopIconsSmall}>
          <img className={styles.IconsS} src={require('../../icons/drop.png')} alt="Glucoze icon" />
          <p>Krvni sladkor</p>
        </div>
        <div className={styles.DesktopIconsSmall}>
          <img className={styles.IconsS} src={require('../../icons/heartbeat.png')} alt="Heartbeat icon" />
          <p>Pritisk</p>
        </div>
        <div className={styles.DesktopIcons}>
          <img className={styles.Icons} src={require('../../icons/pills.png')} alt="Pills icon" />
          <h4 onClick={() => getReceptiHandler()}>Recepti</h4>
        </div>
        <div className={styles.DesktopIcons}>
          <img className={styles.Icons} src={require('../../icons/help.png')} alt="Chat bubble icon" />
          <h4 onClick={() => getMessagesHandler()}>Sporočila</h4>
        </div>
        
      </div>
      {showRecepti ? <Recepti data={userId} /> : null}
      {showMessages ? <Messenger data={userId} /> : null}
      {showWeightGraph ? <WeightGraph data={userId} /> : null}
    </div>
  ) 
}

export default Users;
