import React, { useState, useEffect, useRef } from 'react';
//import { Line } from 'react-chartjs-2';
import moment from 'moment';
import LineChart from './Graph/LineChart';
import styles from '../../styles/WeightGraph.module.css';

import * as firebase from 'firebase';
  
const Weight = (props) => {

  let userId = props.data // getting UID

  // arrey with object with {id, date, weight}
  const [allWeight, setAllWeight] = useState(Array); 
  
  const [weightArray, setWeightArray] = useState(Array); // setting array of weights
  const [dateArray, setDateArray] = useState(Array); // setting array of dates

  
  // *
  // best way to check for mounted component !!
  const isCurrent = useRef(true);

  useEffect(() => {
    return () => {
      isCurrent.current = false;
    }
  }, [])
  // * for checking mounted components
  // *
  // then use 
  // if (isCurrent.current) { setState(XXYY) }  !!

  const getDataFirebase = (userId) => {
    //let array: any;
    let data;
    console.log("checking how many times it renders !!")
    // getting data from location for WEIGHT
    let getData = firebase.database().ref('/users/' + userId + '/weight');
    getData.on('value', function(snapshot) {
      //console.log(snapshot.val());
      data = snapshot.val();
      if (data !== null) {
        // getting data to correct form ID:, DATE:, WEIGHT:
        const allWeightArray = Object.keys(data).map( key => {
          return  { id: key, date: data[key].date, weight: data[key].weight };
        })
        // set in reverse order so the last input is 1 in line
        // checking for componenet is mounted !!
        if (isCurrent.current) { setAllWeight(allWeightArray.reverse()) }
        //console.log(allWeightArray)
        let testDate = [];
        //const dateArray = 
        allWeightArray.map( each => {
          return testDate.push(each.date)
        })
        console.log("datearray?: " + testDate)
        if (isCurrent.current) { setDateArray(testDate.reverse()) }
        let testWeight = [];
        //const weightArray = 
        allWeightArray.map( each => {
          return testWeight.push(each.weight)
        })
        console.log("weightarray?: " + testWeight)
        if (isCurrent.current) { setWeightArray(testWeight.reverse()) }
      }
    })
  }
  

  useEffect(() => {
    getDataFirebase(userId)  //user.uid);
    console.log("Getting data from firebase!")
  }, [userId])

  
  useEffect(() => {
    console.log('rendering allWeights(id, weight, date)', allWeight)
  }, [allWeight]);
  useEffect(() => {
    console.log('rendering weightsArray(only weights)', weightArray)
  }, [weightArray]);
  useEffect(() => {
    console.log('rendering dateArray(only dates)', dateArray)
  }, [dateArray]);
  
  // handling buttons under graph - showing ALL, WEEK, MONTH
  
  const handleAll = () => {
    let testAllDate = [];
    let testAllWeight = [];
    let thisAllArray = [];

    allWeight.forEach( (weight) => {
        thisAllArray.push(weight);
        testAllDate.push(weight.date);
        testAllWeight.push(weight.weight)
    })
    if (isCurrent.current) { setDateArray(testAllDate.reverse()) }
    if (isCurrent.current) { setWeightArray(testAllWeight.reverse()) }
    
    console.log("rendering all data")
  }
  const handleMonth = () => {
    let testMonthDate = [];
    let testMonthWeight = [];
    let thisMonthArray = [];
    let thisMonthStart = moment().subtract(1, 'month').format("YYYY/MM/DD");
    
    allWeight.forEach( (weight) => {
      if (moment(weight.date).isSameOrAfter(thisMonthStart)) {
        thisMonthArray.push(weight);
        testMonthDate.push(weight.date);
        testMonthWeight.push(weight.weight)
      }
    })
    if (isCurrent.current) { setDateArray(testMonthDate.reverse()) }
    if (isCurrent.current) { setWeightArray(testMonthWeight.reverse()) }
      
    console.log("Month");
  }
  const handleWeek = () => {
    let testWeekDate = [];
    let testWeekWeight = [];
    let thisWeekArray = [];
    let thisWeekStart = moment().subtract(1, 'week').format("YYYY/MM/DD");
    
    allWeight.forEach( (weight) => {
      if (moment(weight.date).isSameOrAfter(thisWeekStart)) {
        thisWeekArray.push(weight);
        testWeekDate.push(weight.date);
        testWeekWeight.push(weight.weight)
      }
    })
    if (isCurrent.current) { setDateArray(testWeekDate.reverse()) }
    if (isCurrent.current) { setWeightArray(testWeekWeight.reverse()) }
      
    console.log("week")
  }

  return (
    
    <div className={styles.Desktop}>
      <div className={styles.BeforeChart}>
        <LineChart dateArray={dateArray} weightArray={weightArray}/>
      </div>
      {/*   TESTING 3 buttons for WEEK, MONTH, ALL*/}
      <div className={styles.InChart}>
          <a
            onClick={() => handleWeek()} 
            className={styles.InChartButton}>week</a>
          <a 
            onClick={() => handleMonth()}
            className={styles.InChartButton}>month</a>
          <a 
            onClick={() => handleAll()}
            className={styles.InChartButton}>all</a>
      </div>
      
    </div>
  );
};

export default Weight;