import React from 'react';
import { Line } from 'react-chartjs-2';
  
  //let data: any;
const LineChart = (props) => {
    let labels = props.dateArray; 
    let datas = props.weightArray; 
    let minValue = Math.min( ...props.weightArray ) - 10;
    let maxValue = Math.max( ...props.weightArray ) + 10;
    
    //let minDate = labels[0];
    //let maxDate = labels[labels.length-1];

    // options for LINECHART !!
    const options = {
              responsive: true,
              tooltips: {
                titleFontSize: 12,
                bodyFontSize: 12
              },
              title:{
                display: true,
                text: 'Telesna masa',
                fontColor: '#009fd9'
              },
              legend: {
                display: true,
                position: 'bottom',
                labels: {
                  fontColor: '#009fd9'
                }
              },
              scales: {
                yAxes: [{
                  display: true,
                  position: 'right',
                  gridLines: {
                    display: false,
                  },
                  ticks: {
                    suggestedMax: maxValue,
                    suggestedMin: minValue,
                    stepSize: 20,
                    fontColor: '#009fd9'
                }
                }],
                xAxes: [{
                  gridLines: {
                    display: false,
                  },
                  ticks: {
                    display: false, // doesnt show XAXIS 
                    scaleLabel: {
                      display: true,
                    },
                    
                    // callback: function(value: any, index: any, values: any) {
                    //   //let maxVal: any = values.length;
                    //   //console.log(maxVal)
                    //   if (index !== 1) {
                    //     console.log("i: " + index)
                    //     value = "";
                    //   }
                    //   return value
                      
                    // },
                    fontColor: '#009fd9',
                    
                    autoSkip: true,
                    //stepSize: 2,
                    ////maxTicksLimit: 2,
                    //maxRotation: 2,
                    //max: maxDate,
                    //min: minDate
                  }
                }]
              }
    }

    //DATA for LINECHART !!
    let data = {
      labels: labels,
      datasets: [
        {
          fill: true,
          //lineTension: 0.5,
          backgroundColor: 'rgb(56, 128, 255, 0.3)',
          //backgroundColor: 'rgb(255, 255, 255, 0.3)',
          borderColor: 'rgb(56, 128, 255, 0.5)',
          //borderColor: 'rgb(255, 255, 255, 0.8)',
          borderWidth: 2,
          label: 'Telesna masa',
          //fontColor: 'rgb(56, 128, 255, 0.5)',
          data: datas
          }
      ]
    }
// GRAPH WORKING - FIX DATE INPUT  

  return  <Line data={data} options={options}/>
}

export default LineChart;